<template>
  <div>
    <div v-if="todos.length > 0">
      <span v-for="todo in todos" :key="todo.id">
        {{ todo.title }}
      </span>
    </div>
    <span @click="show.editor = !show.editor" v-else>Add Todo + </span>
    <div v-if="show.editor">
      <input type="text" v-model="editor.title" placeholder="Enter Title" />
      <input type="datetime-local" v-model="editor.time" placeholder="Enter Todo time" />
      <div @click="addTodo" class="butPB">
        <i class="bs-save"></i> Add Todo
      </div>
    </div>
  </div>
</template>
<script>
  import * as serverUtils from "@/utils/serverUtils";
  export default {
    name: "todos",
    mounted() {
      this.init();
    },
    data() {
      return {
        todos: [],
        show: {
          editor: true
        },
        editor: {
          title: '',
          time: ''
        }
      };
    },
    methods: {
      init() {
        serverUtils
          .makeHttpRequest({
            method: "GET",
            url: "/api/v1/todos"
          })
          .then(this.showTodos)
          .catch(this.$parent.showError);
      },
      showTodos(httpObject) {
        this.todos=httpObject.response.data
      },
      addTodo(){
        serverUtils
          .makeHttpRequest({
            method: "POST",
            url: "/api/v1/todos",
            body:{
              title:this.editor.title,
              todoTime:new Date(this.editor.time).getTime()
            }
          })
          .then(this.showTodos)
          .catch(this.$parent.showError);
      }
    }
  };
</script>