export function makeHttpRequest(requestObject) {
  return new Promise((resolve, reject) => {
    var xmlHttp = new XMLHttpRequest();
    xmlHttp.open(requestObject.method, requestObject.url, true);
    var contentType = requestObject.contentType ? requestObject.contentType : findContentType(requestObject.body)
    xmlHttp.setRequestHeader("Content-Type",contentType);
    xmlHttp.onreadystatechange = function () {
      if (xmlHttp.readyState == 4) {
        let serverResponse = xmlHttp.getResponseHeader("Content-Type") != null && xmlHttp.getResponseHeader("Content-Type").includes("application/json") ? JSON.parse(xmlHttp.responseText) : xmlHttp.responseText;
        if (xmlHttp.status >= 200 && xmlHttp.status < 400) {
          resolve({
            response: serverResponse,
            status: xmlHttp.status,
            request: requestObject
          })
        } else {
          reject({
            response: serverResponse,
            status: xmlHttp.status,
            request: requestObject
          })
        }
      }
    };
    if (requestObject.body) {
      if(contentType==='application/json'){
        xmlHttp.send(JSON.stringify(requestObject.body));
      }else{
        xmlHttp.send(requestObject.body);
      }
    } else {
      xmlHttp.send();
    }

  });
}

function findContentType(object) {
  if (object === null || object === undefined) {
    return "text/plain";
  } else if (object.constructor === {}.constructor) {
    return "application/json";
  } else {
    return "application/x-www-form-urlencoded";
  }
}