var functionMap = {}

function init(host, login, logout) {
  setFunction("IAM_1", login);
  setFunction("IAM_2", logout);
  const url = "ws://hari:3000";
  const connection = new WebSocket(url);
  // connection.onopen = () => {
  //  console.log("websocket open")
  // };
  connection.onerror = error => {
    console.log(`WebSocket error: ${error}`);
  };
  connection.onmessage = e => {
    // console.log(`WebSocket Message: ${e.data}`);
    let json = JSON.parse(e.data);
    if (functionMap[json.code]) {
      functionMap[json.code](json);
    }
  };
}

function setFunction(code, func) {
  functionMap[code] = func;
}

module.exports = {
  init,
  setFunction
};